  display: none;
}

/** Media Queries para SASS **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.w-100 {
  width: 100% !important;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode legend, .dark-mode label, .dark-mode blockquote {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .precio {
  color: #71B100;
}
.dark-mode .iconos-caracteristica img {
  filter: invert(50%);
}
.dark-mode .derecha .dark-mode-boton {
  filter: none;
}
.dark-mode td {
  color: #FFFFFF;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.header.inicio h1 {
  display: block;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  display: none;
}
.contenido-header h1 span {
  display: block;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}
.footer .copyright .autor {
  text-transform: uppercase;
  font-weight: bold;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100%);
  cursor: pointer;
  background-color: #FFFFFF;
  padding: 0.5rem;
  border-radius: 50%;
  box-sizing: content-box;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    width: 2rem;
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    align-items: flex-end;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion a {
    font-size: 1.8rem;
  }
}

/* Menu de hambuerguesa  **/
.mobile-menu {
  margin-top: 3rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.contenedor-anuncios {
  word-wrap: break-word;
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}
.anuncio p {
  max-width: 30rem;
}

.precio {
  font-weight: 700;
  color: #71B100;
  font-size: 2.4rem;
}

.iconos-caracteristica {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
  padding: 0;
}

li {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
li p {
  font-weight: 900;
}

.ver-todas {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.boton-amarillo {
  text-transform: uppercase;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-inlineblog {
  text-transform: uppercase;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo-inlineblog:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #910505;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  text-transform: uppercase;
  padding: 1.5rem 0;
}
.boton-rojo-block:hover {
  background-color: #780404;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  text-align: center;
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0rem;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 3rem;
}
.testimonial blockquote {
  padding-left: 7rem;
  position: relative;
}
.testimonial blockquote::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
}
.testimonial p {
  color: #FFFFFF;
  text-align: end;
}

/** Paginas Internas **/
.nosotros blockquote {
  margin: 3rem 0;
  font-weight: 900;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

input,
select {
  display: block;
  width: 100%;
  padding: 1rem 1rem;
  border: 1px solid #e1e1e1;
}

label {
  font-weight: 900;
  margin-top: 1rem;
}

label {
  display: block;
}

textarea {
  display: block;
  width: 100%;
  height: 20rem;
  resize: none;
  border: 1px solid #e1e1e1;
}

fieldset {
  margin-bottom: 2rem;
  border-color: #e1e1e1;
}

input[type=submit] {
  width: auto;
}

.forma-contacto {
  max-width: 50%;
  display: flex;
  align-items: center;
}

.alerta {
  text-align: center;
  padding: 1rem;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 3rem 0;
}
.alerta.alerta {
  background-color: #910505;
  border: 1rem solid #560101;
}
.alerta.correcto {
  background-color: #71B100;
  border: 1rem solid #487200;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  display: block;
  overflow: auto;
  min-height: 50vh;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem 10rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
  margin: 0 auto;
}
table.propiedades tbody td {
  margin-right: 50rem;
}
table.propiedades td {
  text-align: center;
}

@media (min-width: 768px) {
  table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    display: table;
    overflow: auto;
  }
  table.propiedades thead {
    background-color: #71B100;
  }
  table.propiedades thead th {
    color: #FFFFFF;
    padding: 2rem;
  }
  table.propiedades .imagen-tabla {
    width: 10rem;
    margin: 0 auto;
  }
  table.propiedades tbody td {
    margin-right: 50rem;
  }
  table.propiedades td {
    text-align: center;
  }
}
.imagen-small {
  width: 20rem;
  margin-top: 2rem;
}